import Topbar from '../../components/topBar/topbarx.vue'
import {
	uniSwipeAction,
	uniSwipeActionItem,
	uniList,
	uniListItem
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			title: '订单填写',
			topbarData: {
				leftHandle: false,
				title: "订单填写",
				left: "《",
				right: ""
			},
			options: [{
				text: '更多日期 >',
				style: {
					backgroundColor: 'rgba(255,243,247,1)',
					color: '#FF9805'
				}
			}],
			pic: 1, // 票的张数,
			weekList: [], // 星期列表
			weekIsActive: 0, // 星期被选中激活
			date: '', // 使用日期
		};
	},
	onLoad(){
	this.initDate()	
	},
	onUnload(){
		uni.hideLoading()
	},
	onShow() {
		if(Object.keys( this.$store.state.ticketFillDate ).length == 0){
			// 为空
			return
		}else{
			this.date = this.$store.state.ticketFillDate.dateStr
			console.log('选择出来的日期', this.date)
		}
	},
	methods: {
		// 生成使用日期
		initDate(){
			// 设置跨月依然游刃有余 skr
			let time = '2020/5/6'
			console.log('初始化日期time', time)
			let timeSeconds = new Date(time).getTime()
			let oneDate = {
			}
			let box = []
			// return
			// i 小于到数值就是展示多少条日期的数值
			for (let i = 0;i<5;i++) {
				let year = new Date(timeSeconds).getFullYear() // 获取到年
				let month = new Date(timeSeconds).getMonth() + 1 // 获取到月
				let day = new Date(timeSeconds).getDate() // 获取到日期
				let showDay = month + '/' + day
				let week = new Date(timeSeconds).getDay() // 获取到周末
				 oneDate = {
					day:showDay,
					week:week
				}
				box.push(oneDate)
				timeSeconds = timeSeconds + 86400000
			}
			console.log(box)
			this.weekList = box
			
			
		},
		// 改变星期激活
		changeIndex(item, index){
			this.weekIsActive = index
			let year = new Date().getFullYear()
			
			this.date = year + '/' + item.day
			console.log('使用日期', this.date)
		},
		// 选择更多日期
		dateMore(){
			uni.navigateTo({
				url: '../../pages/chosedate/chosedate?type=' + 'ticketFill'
			})
		},
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		onClick(e) {
			// console.log('当前点击的是第'+e.index+'个按钮，点击内容是'+e.content.text)
			console.log(e.index)
		},
		change(open) {
			// console.log('当前开启状态：'+ open)
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			uni.showModal({
				content: '表单数据内容：' + JSON.stringify(formdata),
				showCancel: false
			});
		},
		formReset: function(e) {
			console.log('清空数据')
		},
		// 添加/减少票的张数
		changeNum(i){
			// i = 2 加 i = 1 减
			 if(i === 2) return this.pic++
			 if(i === 1 && this.pic > 1) return this.pic--
		}
	},
	components: {
		Topbar,
		uniSwipeAction,
		uniSwipeActionItem,
		uniList,
		uniListItem
	}
}
